    <div v-else-if="lang===`en`">
    <h4 class="fw-bold">Passer-by, tell Poland ...</h4>
    <p class="fw-bold">
      Raymond Graham Swing, a radio announcer extremely popular in the United States, announced on the afternoon broadcast of May 18, 1944 for the whole of America: "The magnificent flanking maneuver that caused the capture of Cassino was successful thanks to the attack of the Polish troops, the greatest soldiers of the present war."
    </p>
    <p>
      Undoubtedly, the day of planting a white and red flag on the ruins of the Benedictine abbey in Monte Cassino was a great triumph for Polish soldiers. They were distinguished from all the armies fighting for this seemingly impregnable German redoubt, barring the Allies' way to Rome. Indeed, from the beginning of 1944, the best allied forces were bled out here one by one. In the first attack in January, over 2,000 American and French soldiers were killed in the swamps of the Rapido River. The British started the second attack in mid-February. When they bled out on Hill 593, they were replaced by Indians and just as heavily decimated. New Zealanders, including the Maori famous for their bravery, attacked the Liri Valley, and their attack was unsuccessful. After heavy fights at Monte Cassino, the New Zealand corps was disbanded ... In the third, March attack, the town of Cassino was turned into rubble, and the brave Gurkhas were thrown away from the ruins of the monastery - it was incredible! As Melchior Wańkowicz wrote, the famous Indian 4th Infantry Division, which lost 3,000 soldiers in this attack, for the first time in its history did not achieve the goal of the attack. German parachute shooters fully confirmed that they deserved to be called Hitler's military elite. The Allies called them the Green Devils. Impatient and nervous, Winston Churchill wrote to General Harold Alexander, Commander of the 15th Army Group in Italy: “I would like you to explain to me why the passage to Cassino, Mount Monastic, etc. only two or three miles wide is the only place you must hit. Five or six divisions have worn out when they climb into this mouth ... It is very difficult to understand why the most defended place is the only way forward ... " Alexander replied in a long message, explaining as precisely as possible that there was no other way to Rome. He emphasized the very difficult terrain and weather conditions that had been troubling the Allies since last autumn. Finally, he mentioned that his troops must struggle with selected enemy troops. The prime minister replied briefly and sarcastically: "Thank you very much for such a complete explanation ... It is certainly very difficult for the enemy."

    </p>
    <h4 class="fw-bold">2 The Corps goes into action</h4>
    <p>
      After the failure of the third Allied attack on Monte Cassino in the second half of March 1944, the front stagnated. It became clear to the Allies that fresh meals were needed to break the Gustav Line. It is an hour for the 2nd Polish Corps of General Władysław Anders, who entered Italy in December 1943 and became part of the British 8th Army. Churchill then wrote to General Alan Brooke, Chief of the Imperial Staff: "The Poles' entry into battle is now becoming an urgent matter." On March 24, 1944, General Oliver Leese, commander of the 8th Army, met with General Anders in Vinchiaturo. The commander of the 2nd Corps agreed that his soldiers should take part in the fourth, spring attack on the Gustav Line. As General Anders recalls: "Initially, the task assigned to the 2nd Polish Corps by the commander of the 8th Army was broadly defined:" get Monte Cassino and operate on Piedimonte ". Which meant nothing less than to break Gustav's line in its most vulnerable place and hit the strongest redoubt in Hitler's line. General Anders detailed this plan: “I envisioned first of all to break the defensive position above Monte Cassino, farther north, in order to obtain first the complete seclusion of the monastery, which was to be conquered only in the next phase. The subject of the first attack was the main bastions of the defense system: Hill 593 and Colle San Angelo. The mastery of Colle San Angelo was to result in the cloister's complete isolation. " In addition to the 2nd Polish Corps, the British 13th Corps was appointed to attack. The British were to cross the Rapido River and capture the suburbs between Cassino and the Liri River, isolate Cassino from the west and, after opening road number 6, hit the Hitler line. It was predicted that if both corps got the job done, Gustav's line would eventually break. The Moroccans and Tunisians from the French Expeditionary Force, who had to surprise the parachute shooters with an attack in the Aurunci Mountains, had to help them. The retreating Germans were to be crossed by the American 6th Corps with a blow from the bridgehead at Anzio. In total, the allies gathered 21 divisions and 11 independent brigades against 14 divisions and three German brigades. The Allies also prevailed in armor and artillery, and their aviation completely dominated the airspace over the Monte Cassino massif. However, this advantage was still canceled out by the skillful defense of the German parachute and mountain shooters. Churchill was right when he wrote that "the enemy is also very difficult". But as one of the officers of the elite 1st Parachute Rifle Division, Colonel Rudolf Böhmler, recalled: “The paratroopers knew well what was at stake. To keep Cassino under all circumstances was a fight to death. This time, however, an equally determined opponent was going to fight them: none of the nations fighting at Monte Cassino had with the Germans such scores to settle as the Poles.
    </p>
    <h4 class="fw-bold">Blood flows from the mountains</h4>
    <p>
     Almost the whole of April took the transfer and installation of units designated for the fourth attack on the Monte Cassino massif. This gigantic operation was difficult not only for logistical and field reasons. As General Anders wrote: “The conditions of the troops in the defense and starting positions for the attack were very difficult. The entire frontier area was day and night under heavy harassing enemy artillery and mortar fire. The soldier was hidden from primitive folders or large stones. Digging into the rocks was out of the question. " On April 27, General Anders invited the commanders of the two main units of his corps to choose by lot the directions in which they were to attack the Germans. General Bronisław Duch, for his 3rd Carpathian Rifle Division, attacked the hills 593–569, Massa Albanetta and then the monastery. The 5th Kresowa Infantry Division of general Nikodem Sulik was to take over Widmo, San Angello and hills 575 and move to Hitler's line. The day of the "D", or attack, was set for May 11. After a dozen or so hot days, a little rain fell on May 11. Officers had the last briefings and the last details were checked. The orders of the commander-in-chief were read in the units. General Anders concluded his order to the soldiers with the words: "With faith in the righteousness of Divine Providence, we go forward with the holy slogan in our hearts: God, Honor and the Fatherland" .. The commander of the 8th Army, General Leese, ended his order with the words of Admiral Nelson from Trafalgar: "Let everyone do their duty." In the evening they all set off to do their duty: the English and Indians of the 13th Corps on the Rapido, Moroccan and Tunisian highlanders in the Aurunci Mountains, and the Americans on the Tyrrhenian coast. Finally, Polish battalions of the Carpathian and Borderlands began to attack. Already in the first moments of the attack, it turned out that the artillery strike that preceded it did not do much harm to the enemy. The paratroopers opened a murderous fire on the attackers from their bunkers, perfectly integrated into the terrain, masked with boulders and bushes. It was only possible to locate them when they began to blow in series of machine guns and erkaems. General Anders emphasized in his memoirs: “Total darkness of night and smoke. You can't see anything for a few steps. Even soldiers of the same team, in a march or attempting to march forward, falling on fire and jumping up again, after outbursts close to them or among them, lose communication, find each other with poverty, do not count their condition. From the very beginning, the commanders of battalions, companies, and platoons are killed or wounded, and the command is taken by alternates and deputy deputies. " Despite this, the battalions of the 1st Carpathian Rifle Brigade managed to break into hill 593 and approach the so-called The gorge, leading to Massa Albanetta, and start the fight for Hill 569. The 5th Vilnius Infantry Brigade, on the other hand, stormed the hill, named by the Americans during their first attack as Widma. It was a real "molehill" of bunkers, which Polish shooters had to conquer one by one, incurring huge losses. The Kresowiacy tried to make their way towards Hill 575, but they did not get far, as the German fire covered the entire valley. The troops attacking San Angelo were even worse. The decimated companies retreated to Hill 706. It soon turned out that it was easier to take enemy positions than to hold them. The attackers and defenders engaged in a fierce battle that reached an intensity never before seen in this battle. Registered Colonel Ludwik Domoń wrote in his report: “The stubbornness is so great that those who have not seen these conditions cannot understand [them]. For example, I report that Platoon Leader Gorgolewski from 18 L [wowski] B [atalion] S [trzelców], cut by an enemy machine gun, first shoots through his legs into the bunker, and then, shot a second time through his breasts, rises and shouts: "Colleagues, hit those motherfuckers »and only then after a shot by a sharpshooter is he hit in the head with a scream dying on his lips:« Long live Poland »”. Unfortunately, the dedication and insane courage of the Poles did not manage to defeat the German paratroopers this time. It was useless to send the Shermans to the Gorge - they were decimated by mines and fire from anti-tank guns. As General Anders emphasizes, "The immediate introduction of fresh forces into the attack was impossible due to the very small capacity of the front roads and paths and the lack of space in the small front area." A retreat had to be heard, but the first, unsuccessful attack was not entirely in vain. Due to the fact that the Germans had to engage in the fight with Polish soldiers to the maximum extent, the British from the 13th Corps managed to gain bridgeheads and break the leading positions of the "Gustav" line. Colonel Böhmler, who commanded the defense of Hill 593, wrote admiringly in the report: "Carpathian riflemen had a great fight ...". The attack and fight of May 11-14 cost Poles 205 dead, 380 missing and 1000 wounded. The number of the missing persons was decreasing with each passing day, as the soldiers returned from the areas of the recent fighting and even broke out of German captivity.
    </p>
    <h4 class="fw-bold">Red and white is announcing Victoria</h4>
    <p>
      Meanwhile, in the headquarters of the 2nd Corps, General Anders with his officers was already preparing another attack. They are scheduled for 7.00 on May 17. The basic plan and direction of the attack remained unchanged. Polish battalions launched an attack just after the firestorm of their own artillery. The Poles had it so much easier that they jumped over the enemy firewalls, which had already been identified during the first attack, which does not mean that they did not suffer any losses. The soldiers died not only from enemy fire, but also from mines and traps. However, the Carpathians and the Kresovians were unstoppable. On that day, they captured the Spectrum ridge (with the exception of its northern part), as well as Hill 593 and the Gorge. Again, the opponents engaged in a murderous fight, and it seemed that the scenario of the first attack could repeat itself. But the next day, General Anders launched battalions that bled on May 11, reinforced by a commando company and part of the 15th Poznań Uhlans Regiment. Eventually he also dispatched two battalions drawn from gunners, drivers, supplies, cooks, and other rear soldiers. The general recalled: “It was a moment of crisis of battle when both sides, exhausted, lie facing each other, seemingly incapable of effort. Then the stronger will wins, the one who will get the final blow… ”. On the morning of May 18, the Poles took to the battle. Around 6.00 am, the soldiers of the 6th Carpathian Rifle Battalion stormed Masseria Albaneta. At the same time, soldiers from the 5th Battalion were climbing up Hill 569, breaking through the defense of the German shield troops that remained after most of their forces retreated overnight. At 9.00 am, patrols of the 12th Podolian Lancers Regiment set off towards Góra Klasztorna, pulling out of the bunkers the few paratroopers who had not managed to withdraw or stayed with the wounded as paramedics. They were also the first to take possession of the monastery ruins, on which they hung a pennant in the colors of the 12th Regiment - amaranth and navy blue. Around 11.30 am, soldiers from the 3rd platoon of the 3rd company of the 5th battalion of the 3rd Carpathian Rifle Division, under the command of Lieutenant Adam Lorenz, placed a white and red flag in the monastery, "so that everyone would know and see from a distance who had captured the rubble on the top." General Leese soon arrived at General Anders' headquarters with congratulations. With his consent, General Anders ordered the British Union Jack to be placed next to the Polish banner, as a sign of brotherhood in arms with the soldiers of the 13th Corps and as a sign of affiliation of the 2nd Corps to the 8th Army. At that time, fierce fighting continued in the episode of the 5th Kresowa Infantry Division. It was not until the next day, May 19, that Hill 575 was cleared of the enemy. The Germans finally withdrew to Hitler's line. Congratulations were flowing to the staff of the 2nd Corps from everywhere. The newspapers emphasized the combat qualities of Polish soldiers. It seemed for a moment that the Polish cause could no longer be abandoned by the Western Allies. These were all hopes in vain. The 2nd Corps went to a further, equally difficult and bloody campaign, and the Americans and the British to their policy of concessions to Stalin. As a wipe of tears, the words of appreciation of General Alexander remained to the Polish soldiers: "If I had been given a choice between any soldiers I would like to have under my command, he would have chosen you Poles." The Battle of Monte Cassino cost the 2nd Polish Corps 924 dead, 2,930 wounded and 345 missing. In the place of their eternal rest at the stupas of the hill they conquered, there is an inscription: "Passers-by, tell Poland that we have fallen faithful in its service."


    </p>
